import { Component, OnInit, ChangeDetectorRef } from "@angular/core";
import * as firebase from 'firebase/app'; 
import { AngularFireDatabase } from '@angular/fire/database';
import { Router } from "@angular/router";
@Component({
  selector: "app-tables",
  templateUrl: "tables.component.html"
})
export class TablesComponent implements OnInit {
  constructor(
    private afdb:AngularFireDatabase,
    private router:Router,
    private cf:ChangeDetectorRef
  ) {}

  ngOnInit() {    
    //this.usuarios = this.snapshotToArray(usuarios);
    var users = [];
    let usuarios = this.afdb.object('users');
    usuarios.snapshotChanges().subscribe(action => {      
      users.push(action.payload.val());
    });
    setTimeout(() => {
        for(let i in users[0]){
          this.usuarios.push(users[0][i]);
        }
        this.cf.detectChanges();
    }, 1000);
    }

    snapshotToArray(snapshot){
      var returnArray = [];
      snapshot.forEach(element => {
        var item = element.val();
        //item["key"] = element.key
        returnArray.push(item);
      });
      return returnArray;
    }
  
    verDetalleUsuario(user){
      console.log("Este es el objeto del usuario:" , user);
      localStorage.setItem("datosUsuario" , JSON.stringify(user));
      this.router.navigateByUrl("/user");
    }
    usuarios = [];
}
